"use client";

import React, { useContext, useEffect, useState } from "react";
import ApiContext from "../Context/ProductApi/ApiContext";
import { useCart } from "../Context/CartContext/CartContext";
import Link from "next/link";
import Quantity from "../Components/Quantity/Quantity";
import "./cart.scss";

function Cart() {
    const { products } = useContext(ApiContext);
    const { cartItems, updateCartCount } = useCart();
    const [isClient, setIsClient] = useState(false);

    useEffect(() => {
        setIsClient(true);
        console.log("üõí Cart Items:", cartItems);
    }, [cartItems]);

    if (!isClient || !products.length) {
        return <p>Loading...</p>;
    }

    const cartProducts = products.filter((product) => {
        if (!product.id) {
            console.error("‚ùå Product with missing ID detected:", product);
            return false;
        }
    
        const quantity = cartItems[`productcart_${product.id}`] || 0;
        return quantity > 0;
    });
    

    console.log("üõçÔ∏è Filtered Cart Products:", cartProducts);

    return (
        <section className="cart_page">
            <div className="container">
                {cartProducts.length > 0 ? (
                    cartProducts.map((product, index) => {
                        const quantity = cartItems[`productcart_${product.id}`] || 0;
                        const subTotal = product.price * quantity;

                        return (
                            <div key={index} className="cart_item">
                                <div className="cart_item_img">
                                    <Link href={`/product/${product.slug}`}>
                                        <img src={product.images} alt={product.title} />
                                    </Link>
                                </div>
                                <div className="cart_item_name">
                                    <h2>{product.title}</h2>
                                </div>
                                <div className="cart_item_price">
                                    <h2>${product.price}</h2>
                                </div>
                                <div className="cart_item_quantity">
                                    <Quantity productId={product.id} />
                                </div>
                                <div className="cart_item_price">
                                    <h2>${subTotal}</h2>
                                </div>
                                <div className="delete_cart">
                                <button onClick={() => updateCartCount(product.id, 0)}>Delete</button>
                                </div>
                            </div>
                        );
                    })
                ) : (
                    <p>No Items in Cart</p>
                )}
            </div>
        </section>
    );
}

export default Cart;
